<template>
  <div class="flex w-full h-screen flex-col overflow-auto">

    <Head :title="title"></Head>
    <main class="bg-gray-100 flex-1 flex flex-col">
      <div class="topList w-full bg-white h-3 flex">
        <div class="h-full flex flex-col items-center justify-center relative px-1" @click="activateChange(value.id)"
          v-for="value in navList" :key="value.id">
          <div :class="{
            'border-green-500': activateIndex == value.id,
            'hidden': activateIndex !== value.id
          }" class="border-b-3  rounded-[30px] w-2 absolute bottom-0"></div>
          <span :class="{ 'text-green-500': activateIndex == value.id }" class="text-[0.20rem] none">我收到的</span>
        </div>
      </div>
      <div v-if="show" class="flex flex-col items-center flex-1 justify-center">
        <Image src="/src/assets/image/空盒子.png" />
        <span class="text-[12px] font-light ">暂无内容</span>
      </div>
      <div v-else class="my-1">
        <van-cell-group inset>
          <van-cell v-for="value in message" :title="value.message" :value="value.time" :key="value.id">
          </van-cell>
        </van-cell-group>
      </div>
    </main>
  </div>
</template>
<script setup lang='ts'>
// import { ref } from 'vue';
import Head from '@/components/head.vue';
import Image from '@/components/image.vue';
import { noteList } from '@/request/noteList';
const activateIndex = ref<number>(1)
const title = ref<string>('通知列表')
interface navItem {
  id: number;
  navTitle: string
}
const show = ref<boolean>(false)
const message = ref<Array<any>>([])
onMounted(async () => {
  const result = await noteList('/api/noteList', 'get')
  if (result.status == 200) {
    message.value.push({
      id: 1,
      message: result.data.message,
      time: result.data.time,
    })
  }
})
const navList = ref<Array<navItem>>([
  {
    id: 1,
    navTitle: '我收到的'
  }
])

const activateChange = (id: number) => {
  activateIndex.value = id
}
</script>
